@use '../utils/mixins';

.alert-overlay {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);

    .alert-box {
        width: 920px;
        height: 210px;
        background-color: #fff;

        &__title {
           @include mixins.title-style;
        }
    }
}

